.block {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.manage-money {
  width: 20%;
  background-color: rgb(255, 193, 206);
  /* margin: 20rem auto 0 auto; */
  padding: 1rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.manage-act-type {
  font-size: 2rem;
  padding-bottom: 12rem;
}

.buttons {
  display: flex;
  width: 80%;
  justify-content: space-between;
}