.bank {
  width: 31%;
  height: 15rem;
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.bank-name {
  font-family: 'Righteous', cursive;
  font-size: 2rem;
  transition: all .3s;
}

.bank:hover .bank-name {
  font-size: 2.05rem;
}

.bank-city {
  font-family: 'Righteous', cursive;
  font-size: 1rem;
  position: absolute;
  bottom: 1rem;
  right: 3rem;
  transition: all .3s;
}

.bank:hover .bank-city {
  bottom: 0.8rem;
  right: 1rem;
}