.react-button {
  box-sizing: border-box;
  padding: .4rem 1.5rem;
  font-family: 'Righteous', cursive;
  font-size: 1.4rem;
  color: #444444;
  cursor: pointer;
  outline: none;
  border: none;
  border-radius: 5px;
  transition: all .3s;
  overflow: hidden;
  position: relative;
}

.react-button:hover {
  color: #000000;
}

.react-button:focus {
  color: #000000;
}

.react-button::after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0px;
  background-color: '#E09B5B';
  transition: all .2s;
}

.react-button:focus::after {
  height: 3px;
}

.react-button-underline {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  height: 6px;
  width: 100%;
}

.react-disable {
  background-color: '#BDA58E' !important;
}