.account {
  width: 100%;
  display: flex;
  padding: 1rem 1rem 1rem 1rem;
  box-sizing: border-box;
  font-size: 1.5rem;
  border-radius: 10px;
  cursor: pointer;
}

.account-type {
  width: 20%;
}

.account-id {
  flex-grow: 1;
}

.account-balance {
  width: 20%;
}

.button-place {
  width: 10%;
}